  /* reset css */
* {
    margin: 0;
    padding: 0;
    font-size: 18px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
a {
    text-decoration: none;
    color:#000;
}
  /* comman css */
html,body{
    height: 100%;
}
body {
    position: relative;
}
  /* main */
main {
    height: calc(100vh - 40px);
    -webkit-overflow-scrolling: touch;
    color: #000;
}
main section {
    overflow: scroll;
    overflow-x:hidden;
    display: none;
    height: calc(100vh - 40px);
}
.activing {
    display: block;
}
  /* section */
.panel {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    padding: 20px;
    border-bottom: 1px solid #ccc;
}
.panel .cover {
    margin-right: 10px;
}
.panel .cover img {
    width: 70px;
    height: 90px;
}
.panel .content {
    color:#c2c3c4;
}
.content>p:first-child {
    color:black;
}
.panel .title {
    font-weight: bold;
}
.panel .year,.year>span{
    font-size: 14px;
}
.panel .director,.director>span,.actor,.actor>span{
    font-size: 12px;
}
.wrapper{
    width: 100%;
    position: absolute;
    top: 45px;
    bottom: 50px;  /*关键*/
    overflow: hidden;
    z-index: 1;
}
  /* loading */
.loading{
  text-align: center;
  padding: 5px;
  display: none;
}
.loadingSearch {
  text-align: center;
  padding: 5px;
  display: none;
}
.loading .iconfont{
  display: inline-block;
  -webkit-animation:1s linear rotate infinite;
          animation:1s linear rotate infinite;
}
.loadindSearch .iconfont {
  display: inline-block;
  -webkit-animation:1s linear rotate infinite;
          animation:1s linear rotate infinite;
}
@-webkit-keyframes rotate {
0% {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
100% {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg)
}
}
@keyframes rotate {
0% {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
100% {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg)
}
}
  /* searchPage */
.search{
  padding: 5px;
}
.searchBox{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  padding: 5px;
}
.searchBox input {
  border:1px solid #fd0056;
  width: 90%;
}
.searchBox a {
  padding: 10px 30px;
  background: #fd0056;
  color: #fff;
  display: block;
}
.searchBox a span {
  font-size: 20px;
}
.searchList {
  margin-top: 20px;
}
  /* footer */
footer {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    position: fixed;
    bottom: 0;
    z-index: 999;
    background: #fff; 
    height: 40px;
    border-top: 1px solid #ccc;
}
.active {
    color: #fd0056;
}
footer  div {
    -webkit-box-flex:1;
        -ms-flex:1;
            flex:1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex; 
    -webkit-box-pack: center; 
        -ms-flex-pack: center; 
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width:calc(100%/3);
}
footer  div > span {
    display: block;
}
footer .iconfont {
    margin-top: 5px;
    margin-right: 5px;
}
