    url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
    url('//at.alicdn.com/t/font_841644_9wus2xznyww.ttf?t=1538138788630') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
    url('//at.alicdn.com/t/font_841644_9wus2xznyww.svg?t=1538138788630#iconfont') format('svg'); /* iOS 4.1- */
}
